body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: red;
  color: white;
  text-align: center;
  z-index: 10;
}

.footer header{
  background: #ffffff;
}

.footer header .MuiTabs-flexContainer a{
  color: black;
}

.footer .MuiTab-textColorPrimary.Mui-selected{
  color: #ffb200 !important;
}

.footer .PrivateTabIndicator-colorPrimary-108{
  background-color: #ffb200 !important;
}

.footer .MuiTab-wrapper{
  text-transform: capitalize;
}

.page-center{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35vh;
}

.page-center h1{
  text-align: -webkit-center;
  text-align: center;
}

.page-center span{
  color: #ffb200;
  text-transform: uppercase;
}

.homepage .slider-content{
  width: 100% !important;
  background-size: 100%;
  height: 100% !important;
}

.homepage .slider-text{
  display: flex;
  margin-left: 20%;
  margin-top: 30vh;
}

.homepage .slider-text h1{
  color: #ffb200;
}

.homepage .slider-text{
  color: #ffffff;
}

.homepage .swiper-pagination-bullet-active{
  background: #ffb200 !important;
}

.swiper-container{
  height: 100vh;
}


